import { supabase } from "@/supabaseClient"; // <-- you said you already have supabaseClient.ts

type CategoryRow = {
  id: number;
  slug: string;
  img: string | null;
  name_so: string;
  name_en: string;
};

type SubcategoryRow = {
  id: number;
  category_id: number;
  slug: string;
  img: string | null;
  name_so: string;
  name_en: string;
};

export type ProductRow = {
  id: number;
  name: string;
  slug: string;
  base_price: number | null;
};

export type VariantRow = {
  id: number;
  product_id: number;
  label: string;
  price: number;
};

export async function getCategoriesWithSubcategories() {
  const { data: cats, error: catErr } = await supabase
    .from("categories")
    .select("id,slug,img,name_so,name_en")
    .order("id", { ascending: true });

  if (catErr) throw catErr;

  const { data: subs, error: subErr } = await supabase
    .from("subcategories")
    .select("id,category_id,slug,img,name_so,name_en")
    .order("id", { ascending: true });

  if (subErr) throw subErr;

  const categories = (cats ?? []) as CategoryRow[];
  const subcategories = (subs ?? []) as SubcategoryRow[];

  return categories.map((cat) => ({
    ...cat,
    subcats: subcategories.filter((s) => s.category_id === cat.id),
  }));
}

export async function getProductsByIds(ids: number[]) {
  const unique = Array.from(new Set(ids)).filter(Boolean);
  if (unique.length === 0) return [];

  const { data, error } = await supabase
    .from("products")
    .select("id,name,slug,base_price")
    .in("id", unique);

  if (error) throw error;
  return (data ?? []) as ProductRow[];
}

export async function getVariantsByIds(ids: number[]) {
  const unique = Array.from(new Set(ids)).filter((x) => x != null) as number[];
  if (unique.length === 0) return [];

  const { data, error } = await supabase
    .from("product_variants")
    .select("id,product_id,label,price")
    .in("id", unique);

  if (error) throw error;
  return (data ?? []) as VariantRow[];
}

export async function createOrder(args: {
  customer_id: number | null;
  delivery: { name: string; phone: string; address: string; note?: string };
  total_amount: number;
}) {
  const { data, error } = await supabase
    .from("orders")
    .insert({
      customer_id: args.customer_id,
      delivery_name: args.delivery.name,
      delivery_phone: args.delivery.phone,
      delivery_address: args.delivery.address,
      delivery_note: args.delivery.note ?? null,
      total_amount: args.total_amount,
      status: "PENDING",
    })
    .select("id")
    .single();

  if (error) throw error;
  return data as { id: number };
}

export async function createOrderItems(orderId: number, items: Array<{
  productId: number;
  variantId: number | null;
  qty: number;
  priceAtPurchase: number;
}>) {
  const payload = items.map((it) => ({
    order_id: orderId,
    product_id: it.productId,
    variant_id: it.variantId,
    quantity: it.qty,
    price_at_purchase: it.priceAtPurchase,
  }));

  const { error } = await supabase.from("order_items").insert(payload);
  if (error) throw error;
}
